.lvl{
    font-size: 60px;
    color: wheat;
    width: 100%;
    text-align: center;
    font-family:monospace
  }
  .gamearea{
    width: 100%;
  }
  .content {
    margin: 1% 4% 2% 4%;
    width: 100px;
    height: 100px;
    background-color: white;
    box-shadow: 0 0 15px rgba(0,0,0,0.1);
    border-radius: 5px;
    transition: transform 0.5s;
    transform-style: preserve-3d;
  }
  .content1 {
    margin: 1% 4% 2% 4%;
    width: 100px;
    height: 100px;
    background-color: white;
    box-shadow: 0 0 15px rgba(0,0,0,0.1);
    border-radius: 5px;
    transition: transform 0.5s;
    transform-style: preserve-3d;
    transform: rotateY( 180deg ) ;
  }
  
  
  .front,
  .back {
    position: absolute;
    height: 100%;
    width: 100%;
    line-height: 100px;
    color: #03446A;
    text-align: center;
    font-size: 30px;
    border-radius: 5px;
    backface-visibility: hidden;
  }
  
  .back {
    background: #03446A;
    color: white;
    transform: rotateY( 180deg );
  }
  .threeinone{
    padding: 3% 33% 0 36%;
  }
  .fourinone{
    padding: 3% 30% 0 30%;
  }
  .fiveinone{
    padding: 3% 20% 0 20%;
  }
  .life{
    width: 100%;
  }
  .life1{
    transform: translateY(280px);
  }
  .life2{
    transform: translateY(170px);
  }
  .life3{
    transform: translateY(160px);
  }
  .life4{
    transform: translateY(145px);
  }
  .life5{
    transform: translateY(40px);
  }
  .image{
    padding : 0 5px 0 5px;
  }
  .head1{
    font-size: 100px;
    
    font-family: "Pixelify Sans", sans-serif;
    text-align: center;
    padding-top: 1%;
  }
  .heads{
    color: green;
  }
  .headf{
    color: red;
  }
  .sad{
    padding-left: 44%;
  }